import Loading from './brick/Loading'
import TitleBar from './bar/Title'
import BottomBar from './bar/Bottom'
import P42 from './panel/P42'
import {post, globalData} from "./method.js"
import { Order } from './Order.js'
export default {
  components: {
    TitleBar,
    BottomBar,
    Loading,
    P42
  },
  methods: {
    quitApp: function(url) { //
      const _this = this;
      const postData={userName:this.userName,password:this.password};
      Order.$emit('Loading', 'show')
      post("http://localhost:9999/loginout",postData,function(data){
        //
        Order.$emit('Loading', 'hide')
        //
        window.location.href="#/Quit";
      });
    }
  },
  data () {
    return {
      list: [
        { icon: '&#xe600;', title: '', color:'#61c2ff', id:"1000",url:"/Sett"}, 
        { icon: '&#xe629;', title: '', color:'#ffd217', id:"1001",url:"/Help"},
        { icon: '&#xe60e;', title: '', color:'#1bee47', id:"1002",url:"/Version"}
      ],
      userName: globalData.userData.userName,
      sexList:["","",""],
      sex:""
    }
  }
}



